import "core-js";
import React, { useState } from "react";
import styled from "styled-components";
import Badge from '@mui/material/Badge';
import { Search, ShoppingCartOutlined } from "@mui/icons-material";
import { Link } from "react-router-dom";
import { useSelector } from 'react-redux'
import { appReducer } from "../redux/store";


const Container = styled.div`
  height: 60px;
`;

const Wrapper = styled.div`
  padding: 10px 20px;
  display: flex; // aligns left right and center tags in one horizontal line
  align-items: center;
  justify-content : space-between; // left right and center are space out
`;

const Left = styled.div`
  display: flex;
  flex:1;
  align-items: center;
  padding-bottom: 15px;
`
const Language = styled.span`
 font-size: 15px;
 cursor: pointer;  
`

const SearchContainer = styled.div`
  border: 1px solid lightgray;
  display: flex;
  align-items: center;
  margin-left: 25px;
  padding: 5px;
`

const Input = styled.input`
border: none;
font-size: 14px;
&:focus {
        outline: none;
    }
`

const Center = styled.div`
  flex:1;
  font-family: 'Sacramento', cursive;
  font-size: 40px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
`
const Right = styled.div`
  flex:1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`
const MenuItem1 = styled.div`
  font-size: 14px;
  cursor: pointer;
  margin-left: 25px;
  visibility: ${props => props.user === null ? "visible" : "hidden"}; 
   //if user is logged in , hide register and login button
`

const LogOut = styled.div`
  font-size: 14px;
  cursor:pointer;
  margin-left: 25px;
  visibility: ${props => props.user === null ? "hidden" : "visible"}; 
   //if user is logged in , make log out visible
`
const MenuItem = styled.div`
  font-size: 14px;
  cursor:pointer;
  margin-left: 25px;
`

const Navbar = () => {
  const quantity = useSelector(state => state.cart.quantity);

  console.log(quantity)
  const user = useSelector(state => state.user.currentUser);
  const loginstatus = useSelector((state) => state.user.logInStatus)
  console.log(loginstatus)


  const handleClick = (e) => {
    appReducer(undefined, 'LOG OUT');
    loginstatus = false;
  };

  return (<Container>
    <Wrapper>
      <Left>
        <Language>EN</Language>
        <SearchContainer>
          <Input placeholder="search" />
          <Search style={{ color: "gray" }}></Search>
        </SearchContainer>
      </Left>
      <Link to={`/`} style={{ textDecoration: 'none', color: 'black' }}>
        <Center>The Bohemian Paradise</Center>
      </Link>

      <Right>
        {/* user={user} */}

        <Link to="/register" style={{ textDecoration: 'none', color: 'black' }} >
          <MenuItem1 user={user}  >REGISTER</MenuItem1>
        </Link>

        <Link to="/login" style={{ textDecoration: 'none', color: 'black' }}>
          <MenuItem1 user={user} >LOG IN</MenuItem1>
        </Link>
        
        {user && <Link to="/" style={{ textDecoration: 'none', color: 'black' }}>
          <LogOut user={user} onClick={handleClick} >LOG OUT</LogOut>
        </Link>}


        <Link to="/cart">
          <MenuItem>
            <Badge badgeContent={quantity} color="primary">
              <ShoppingCartOutlined color="action"></ShoppingCartOutlined>
            </Badge>
          </MenuItem>
        </Link>

      </Right>
    </Wrapper>
  </Container>

  );
};

export default Navbar;
