import React from 'react'
import styled from "styled-components"

const Container = styled.div`
    height: 70vh;
    background-color: #fcf5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`
const Title = styled.h1`
    font-size: 60px;
    margin-bottom: 20px;
`
const Description = styled.p`
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 20px;

`
const InputContainer = styled.div`
    width: 50%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`
const Input = styled.input`
    border: none;
    width: 50%;
    height: 12%;
    padding: 0px 10px;
    margin-bottom: 20px;
    font-size: 18px;
`
const Message = styled.textarea`
    border: none;
    font-size: 18px;
    width: 55%;
    height: 50%;
    margin-bottom:  20px;
    padding: 10px;
    resize: none;
`
const Button = styled.button`
  width: 100px;
  padding: 5px ;
  border: none;
  border-bottom: 5px solid #028686;
  border-radius: 8px;
  /* background: #fab70f; */
  background: #04acac;
  color: white;
  font-size: 1.7rem;
  font-weight: bold;
  /* font-family: 'Raleway', sans-serif; */
  text-shadow: 0 -1px 0 rgba(0,0,0,0.3);
  outline: none;
  cursor: pointer;
  transition-property: background, color;
  transition-duration: 0.08s;
  &:hover {
    background: #029494;
    border-bottom-color: #017070;
  }
  &:active {
    bottom: -3px;
    border-bottom: 2px solid #005f5f;
    background: #018080;
    color: #e2d8d8;
  }
`

const NewsLetter = () => {
    return (
        <Container>
            <Title>Want an artwork of your choice?</Title>
            <Description>Drop us a message and we will contact you</Description>
            <InputContainer>
                <Input placeholder="Your Email" />
                <Message placeholder="Your Message" />
                <Button>
                    SEND
                </Button>
            </InputContainer>

        </Container>
    )
}

export default NewsLetter