
import styled from 'styled-components'
import Announcement from '../components/Announcement'
import Navbar from "../components/Navbar"
import NewsLetter from "../components/NewsLetter"
import Footer from "../components/Footer"
import ProductsPaging from '../components/ProductsPaging'
import { useLocation } from 'react-router-dom'
import { useState } from 'react'

const Container = styled.div`
    
`
const Title = styled.h1`
    margin: 20px;
    margin-top: 40px;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
`
const FilterContainer = styled.div`
    display: flex;
    justify-content: space-between;
`
const Filter = styled.div`
    margin: 20px;
`

const FilterText = styled.span`
    font-size: 22px;
    font-weight: 600;
    margin-right: 15px;
`
const Select = styled.select`
    padding: 10px;
    margin-right: 20px;
    font-size: 17px;
`
const Option = styled.option`
   
`
const ProductList = () => {

    const location = useLocation();
    const category = location.pathname.split('/')[2];
    const [filters, setFilters] = useState('');
    const [sort, setSort] = useState("newest");

    const handleFilters = (e) => {
        const value = e.target.value;
        setFilters( value.toLowerCase());
    }

    return (
        <Container>
            <Announcement />
            <Navbar />
            <Title>COLLECTION</Title>
            <FilterContainer>
                <Filter>
                    <FilterText>Filter Products : </FilterText>
                    <Select name="category" onChange={handleFilters}>
                        <Option disable >Category</Option>
                        <Option>Zodiac</Option>
                        <Option>Valentine</Option>
                        <Option>Nature</Option>
                        <Option>Wildlife</Option>
                        <Option>Monochrome</Option>
                        <Option>Fruits</Option>
                        <Option>Potraits</Option>
                        <Option>Abstract</Option>
                        <Option>Seascapes</Option>
                        <Option>Attractions</Option>
                    </Select>
                </Filter>

                <Filter>
                    <FilterText>Sort Products : </FilterText>
                    <Select onChange={e => setSort(e.target.value)}>
                        <Option value="newest"> Newest</Option>
                        <Option value="asc">Price (asc.)</Option>
                        <Option value="desc">Price (desc.)</Option>
                    </Select>
                </Filter>

            </FilterContainer>
            <ProductsPaging  category={category} filters={filters} sort={sort} />
            <NewsLetter />
            <Footer />
        </Container>
    )
}

export default ProductList