import React from 'react'
import styled from 'styled-components'
import { Link } from "react-router-dom";

const Image = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover ;
    opacity: 0.4;
    z-index: 2;
    
`

const Container = styled.div`
    flex:1;
    margin: 18px;
    min-width: 300px;
    height:  40vh;
    position: relative;
    align-items: center;
    justify-content: center;
    &:hover ${Image}{
        opacity: 0.55;
    }
`


const Info = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    z-index: 3;
    ${Image}:hover & {
        opacity: 1;
    }
`
const Title = styled.h1`
    color: black;
    margin-bottom: 20px;
    font-weight: 900;
    letter-spacing: 1px;
    font-size: 35px;
    -webkit-text-stroke-width: 0.3px;
    -webkit-text-stroke-color: white;
`
const Button = styled.button`
    border: none;
    outline: none;
    padding: 10px;
    background-color: white;
    color: grey;
    font-weight: 600;
    font-size: 13px;
    cursor: pointer;
    transition: all 0.5s ease;
    &:hover{
        background-color: #ecf5f5;
        border: 0.5px solid black;
        border-color: black;
        color: black;
        transform: scale(1.1);
    }  
`

const CategoryItem = ({ item }) => {
    return (
        <Container>
            <Link to={`/products/${item.category}`}>
                <Image src={item.img} />
                <Info>
                    <Title>{item.title}</Title>
                    <Button>EXPLORE</Button>
                </Info>
            </Link>
        </Container>
    )
}

export default CategoryItem