import React, { useEffect } from 'react'
import styled from 'styled-components'
import ProductItem from './ProductItem'
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Navigation } from 'swiper';
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import 'swiper/components/navigation/navigation.min.css'
import './swiperNew.css'
import { useState } from 'react'
import {axiosInstance} from "../config";


SwiperCore.use([Navigation]);

const BigContainer = styled.div`
    padding: 20px;
    height: 70vh;
    background-color: #fcf5f5;
`

const Container = styled.div`
    display : flex;
    padding: 20px;
    justify-content: space-between;
`

const TextContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 20px;
`
const Titletext = styled.h1`
  display: flex;
  font-size: 5vh;
`

const Products = () => {
    const [products, setProducts] = useState([]);
    useEffect(() => {
        const getProducts = async () => {
            try {
                const res = await axiosInstance.get("https://tbp.onrender.com/api/products/")
                // console.log("res ", res.data)
                setProducts(res.data);
            }
            catch (err) { }
        };
        getProducts();
        // console.log(products)

    }, [])

    //get last 10 products = latest products
    // console.log("now : ", products.slice(Math.max(products.length - 2, 0)));
    return (

        <BigContainer>
            <TextContainer>
                <Titletext>LATEST ARTWORKS.</Titletext>
            </TextContainer>

            <Container>
                <Swiper
                    // modules={[Navigation, Pagination, Scrollbar, A11y]}
                    spaceBetween={50}
                    slidesPerView={4}
                    navigation
                    // pagination={{ clickable: true }}
                    // scrollbar={{draggable: true}}
                    onSlideChange={() => console.log('slide change')}
                    onSwiper={(swiper) => console.log(swiper)
                    }
                >
                    {products.slice(Math.max(products.length - 10, 0)).map(item => (
                        <SwiperSlide><ProductItem item={item} key={item.id} /> </SwiperSlide>
                    ))}
                </Swiper>
            </Container>
        </BigContainer>
    )
}

export default Products