import React, { useState } from 'react'
import styled from "styled-components"
import { ArrowLeftRounded, ArrowRightRounded } from "@mui/icons-material";
import { sliderItems } from '../data';
import { Link } from "react-router-dom";


const Container = styled.div`
    width: 100%;
    height: 100vh;
    display:flex;
    position: relative;
    background-color: white;
    overflow: hidden;
    padding-bottom: 30px;
`

const Arrow = styled.div`
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: teal;
  border-radius: 50%;
  top:0;
  bottom:0;
  margin: auto;
  left: ${props => props.direction === "left" && "10px"};  //Here styled(react) component is accepting a prop and returning what
                                                            //should appear on the screen ,i.e left arrow position
  right: ${props => props.direction === "right" && "10px"};
  cursor: pointer;
  opacity: 0.5;
  z-index: 2;  // arrow is above div
`

const Wrapper = styled.div`
  height: 100%;
  display: flex;
  transition:  all 1s ease;
  transform: translateX( ${props => props.slideIndex * - 98.9}vw);
`

const Slide = styled.div`
  width: 98.9vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
`
// const ImgContainer = styled.div`
//   height: 100%;
//   width: 100%;
//   /* flex:1; */
//   display: flex;
//   justify-content: space-between;
// `

//First Slide --> left image + middle text container + right image
const ImgLeft = styled.img`
  height: 100%;
  display: flex;
  align-items: center;
`

const InfoContainer = styled.div`
  flex:1;
  padding: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`

const Title = styled.h1`
  font-size: 8vh;
`

const Description = styled.p`
  margin: 40px 0px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  text-align: center;
`
const Button = styled.button`
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 500;
  background-color: teal;
  color: white;
  cursor: pointer;
  border-radius: 50px 50px;
  border: none;
  outline: none;
  &:hover { 
    background-color: #029b9b;
  }
  
`
const ImgRight = styled.img`
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`

const Slider = () => {
  const [slideIndex, setSlideIndex] = useState(0);  //useState hook used here
  // the update function of slideIndex is called when either left or right arrow is clicked
  const handleClick = (direction) => {
    if (direction === "left") {
      setSlideIndex(slideIndex > 0 ? slideIndex - 1 : 2);
    }
    else {
      setSlideIndex(slideIndex < 2 ? slideIndex + 1 : 0);
    }
  }

  return (
    <Container>
      <Arrow direction="left" onClick={() => handleClick("left")}>
        <ArrowLeftRounded fontSize='large' style={{ fill: "#fff7f7", fontSize: '250%' }} />
      </Arrow>
      {/* slide Index is the index value of each slide in the array we have created and we have initialized it with 0 in the 
      usestate hook */}
      <Wrapper slideIndex={slideIndex}>
        {sliderItems.map(item => (
          <Slide key={item.id}>
            <ImgLeft src={item.imgleft}></ImgLeft>

            <InfoContainer>
              <Title style={{ color: "gray", fontSize: "50px" }}>{item.title[0]}</Title>
              <Title style={{ fontWeight: 900 }}>{item.title[1]}</Title>
              <Description>{item.desc}</Description>
              <Link to={`/products`}>
                <Button>{item.buttonTxt}</Button>
              </Link>

            </InfoContainer>

            <ImgRight src={item.imgright} ></ImgRight>
          </Slide>
        ))}
      </Wrapper>
      <Arrow direction="right" onClick={() => handleClick("right")}>
        <ArrowRightRounded fontSize='large' style={{ fill: "#fff7f7", fontSize: '250%' }} />
      </Arrow>

    </Container>
  )
}

export default Slider