import React, { useEffect } from 'react'
import styled from 'styled-components'
import ProductPagingItem from './ProductPagingItem'
import { useState } from 'react'
import ReactPaginate from 'react-paginate'
import './productPaging.css'
import {axiosInstance} from "../config";


const Container = styled.div`
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: start;
`

const PaginateContainer = styled.div`
  width: 98.9vw;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
  margin-bottom: 20px;

`


const ProductsPaging = ({ category, filters, sort }) => {
    console.log(category, filters, sort);
    const [products, setProducts] = useState([]);
    const [filteredProducts, setFilteredProducts] = useState([]);

    //looking up based on category from the home page
    useEffect(() => {
        const getProducts = async () => {
            try {
                const res = await axiosInstance.get(category ? `https://tbp.onrender.com/api/products?category=${category}` : "https://tbp.onrender.com/api/products");
                // console.log(res)
                setProducts(res.data);
            }
            catch (err) { }
        };
        // console.log(filteredProducts)
        getProducts();
        // console.log(filteredProducts)
    }, [category]);

    // looking up products based on filters
    useEffect(() => {
        const getFilteredProducts = async () => {
            try {
                const res = await axiosInstance.get(filters ? `https://tbp.onrender.com/api/products?category=${filters}` : category ? `https://tbp.onrender.com/api/products?category=${category}` : "https://tbp.onrender.com/api/products");
                // console.log(res)
                setFilteredProducts(res.data);
            }
            catch (err) { }
        };
        getFilteredProducts();
    }, [products, category, filters])

    useEffect(() => {
        if (sort === "newest") {
            setFilteredProducts((prev) =>
                [...prev].sort((a, b) => a.createdAt - b.createdAt)
            );
        }
        else if (sort === "asc") {
            setFilteredProducts((prev) =>
                [...prev].sort((a, b) => b.price - a.price));
        }
        else {
            setFilteredProducts((prev) =>
                [...prev].sort((a, b) => a.price - b.price));
        }
        // console.log(filteredProducts)
    }, [sort]);

    const [pageNumber, setPageNumber] = useState(0);
    const productsPerPage = 12;
    const pagesVisited = pageNumber * productsPerPage;

    const displayProduct = filteredProducts.slice(pagesVisited, pagesVisited + productsPerPage).map(item => {
        return (
            <ProductPagingItem item={item} key={item.id} />
        )

    })

    const pageCount = Math.ceil(products.length / productsPerPage)//Rounds up value
    const changePage = ({ selected }) => {
        setPageNumber(selected)
    }
    return (
        <Container>
            <Container>
                {displayProduct}
            </Container>
            <PaginateContainer>
                <ReactPaginate
                    previousLabel={"Previous"}
                    nextLabel={"Next"}
                    pageCount={pageCount}
                    onPageChange={changePage}
                    containerClassName={"paginationBttns"}
                    previousLinkClassName={"previousBttn"}
                    nextLinkClassName={"nextBttn"}
                    disabledClassName={"paginationDisabled"}
                    activeClassName={"paginationActive"}
                />
            </PaginateContainer>
        </Container>
    )
}

export default ProductsPaging