import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import Announcement from '../components/Announcement'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import NewsLetter from '../components/NewsLetter'
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Navigation } from 'swiper';
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import 'swiper/components/navigation/navigation.min.css'
import { useLocation } from 'react-router-dom'
import { publicRequest } from "../requestMethods"
import { addProduct } from '../redux/cartRedux'
import { useDispatch } from 'react-redux'


SwiperCore.use([Navigation]);

const Container = styled.div`
    
`
const Wrapper = styled.div`
    padding: 50px;
    display: flex;
`
const ImgContainer = styled.div`
    flex: 1;
    width: 40%;
    height: 20%;
`
const Image = styled.img`
    width: 100%;
    height: 90vh;
    object-fit: scale-down;
   
`
const InfoContainer = styled.div`
    flex:1;
    padding: 0px 50px;
`
const Title = styled.h1`
    font-size: 40px;
    font-weight: 500;
`
const Desc = styled.p`
    margin: 20px 0px;
    font-size: 20px;
    font-weight: 400;
`
const Price = styled.span`
    font-weight: 300;
    font-size: 25px;
`

const FilterContainer = styled.div`
    display: flex;
    width: 50%;
    margin: 30px 0px;
    justify-content: space-between;
`
const Filter = styled.div`
    display: flex;
    align-items: center;
`
const FilterTitle = styled.span`
    font-size: 20px;
    font-weight: 600;
`
const FilterSize = styled.select`
    margin-left: 10px;
    padding: 8px;
    cursor: pointer;
    font-size: 17px;
`
const SizeOption = styled.option`
    cursor: pointer;
    font-size: 17px;
`

const Button = styled.button`
    padding: 12px;
    border: 1.5px solid #029494;
    background-color: white;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    transition: 0.2s all ease;
    &:hover{
        background-color: #029494;
        color: white;
    }
    &:active {
    background: #016e6e;
  }
`

const WallImage = styled.img`
    width: 100%;
    height: 90vh;
    object-fit: cover;
    position: relative;
`
const InsideFrame = styled.img`
    width: 35%;
    height: 48%;
    object-fit: cover;
    background-color: green;
    position: absolute;
    top: 90px;
    left: 243px;

`

const Product = () => {

    const location = useLocation();
    const id = location.pathname.split("/")[2];
    //fetching product info by id

    const [product, setProduct] = useState({});
    const [img, setImg] = useState([]);
    const [availableSize, setAvailableSize] = useState([]);
    const [size, setSize] = useState("");
    const quantity = 1;

    const dispatch = useDispatch();

    // console.log(id);

    useEffect(() => {
        const getProduct = async () => {
            try {
                const res = await publicRequest.get("/products/find/" + id)
                setProduct(res.data)
                setImg(res.data.img)
                setAvailableSize(res.data.size)
            } catch { }
        }
        getProduct();
    }, [id])

    // console.log(product);
    // console.log(availableSize);

    const handleClick = () => {
        //update cart
        dispatch(addProduct({ ...product, quantity, size }))

    }

    return (
        <Container>
            <Announcement />
            <Navbar />
            <Wrapper>
                <ImgContainer>
                    <Swiper
                        // modules={[Navigation, Pagination, Scrollbar, A11y]}
                        spaceBetween={0}
                        slidesPerView={1}
                        navigation
                        // pagination={{ clickable: true }}
                        // scrollbar={{draggable: true}}
                        onSlideChange={() => console.log('slide change')}
                        onSwiper={(swiper) => console.log(swiper)
                        }
                    >
                        {/* Main Image */}
                        <SwiperSlide><Image src={img[0]} /> </SwiperSlide>
                        {/* Instagram Post Image */}
                        <SwiperSlide>
                            <WallImage src="/images/walltemplate.jpg" />
                            <InsideFrame src={img[0]} />
                        </SwiperSlide>
                        {/* Image on frame */}
                        <SwiperSlide><Image src={img[1]} /> </SwiperSlide>

                    </Swiper>
                    {/* <Image src="/images/Categories/seascapes.jpg" /> */}
                </ImgContainer>
                <InfoContainer>
                    <Title>{product.title}</Title>
                    <Desc>{product.desc}</Desc>
                    <Price> Rs. {product.price}</Price>
                    <FilterContainer>
                        <Filter>
                            <FilterTitle>Size : </FilterTitle>
                            <FilterSize onChange={(e)=> setSize(e.target.value)}>
                                {availableSize.map((s) => (
                                    <SizeOption key={s}>{s}</SizeOption>
                                ))}
                            </FilterSize>
                        </Filter>
                    </FilterContainer>
                    <Button onClick={handleClick}>ADD TO CART</Button>
                </InfoContainer>

            </Wrapper>
            <NewsLetter />
            <Footer />
        </Container>
    )
}

export default Product