import { Instagram, MailOutline, Phone, Pinterest, Room } from '@mui/icons-material'
import React from 'react'
import styled from "styled-components"

const Container = styled.div`
    display: flex; // aligns left right and center tags in one horizontal line
    align-items: center;
    justify-content : space-between; // left right and center are space out
`;
const Left = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
    /* margin-right: 200px; */
`;
const Logo = styled.h1`
    font-size: 28px;
`;
const Desc = styled.p`
    margin: 20px 0px;
`;
const SocialContainer = styled.div`
    display: flex;
`;
const SocialIcon = styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: white;
    background-color: #${props => props.color};
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
`;

const Center = styled.div`
    flex: 1;
    padding: 20px;
    /* margin-right: 200px; */
    margin-left: 200px;
`;

const Title = styled.h3`
    margin-bottom: 30px;
`;
const List = styled.ul`
    margin:0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    font-size: 17px;
`;
const ListItem = styled.li`
    width: 50%;
    margin-bottom: 10px;
    cursor: pointer;
`;
const Right = styled.div`
    flex: 1;
    padding: 20px;
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    font-size: 17px;
`;

const ContactItem = styled.div`
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    
`

const Footer = () => {
    return (
        <Container>
            <Left>
                <Logo>THE BOHEMIAN PARADISE</Logo>
                <Desc>Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas, corporis similique tempora, porro qui molestiae labore aliquam odit accusamus saepe eveniet quod ipsum aspernatur cupiditate! Corrupti culpa quos ad fuga.</Desc>
                <SocialContainer>
                    <SocialIcon color="E4405F">
                        <Instagram></Instagram>
                    </SocialIcon>
                    <SocialIcon color="E60023">
                        <Pinterest></Pinterest>
                    </SocialIcon>
                </SocialContainer>
            </Left>

            <Center>
                <Title>Useful Links</Title>
                <List>
                    <ListItem>Home</ListItem>
                    <ListItem>Collection</ListItem>
                    <ListItem>Cart</ListItem>
                    <ListItem>Track Order</ListItem>
                    <ListItem>My Account</ListItem>
                    <ListItem>Wishlist</ListItem>
                    <ListItem>Terms and Conditions</ListItem>
                </List>
            </Center>

            <Right>
                <Title>Contact</Title>
                <ContactItem>
                    <Room style={{ marginRight: "10px" }} />
                    Pitampura, New Delhi
                </ContactItem>
                <ContactItem>
                    <Phone style={{ marginRight: "10px" }} />
                    +91 9457661493
                </ContactItem>
                <ContactItem>
                    <MailOutline style={{ marginRight: "10px" }} />
                    agarwal.udit1999@gmail.com
                </ContactItem>
            </Right>

        </Container>
    )
}

export default Footer