import React, { useState } from 'react'
import styled from 'styled-components'
import Announcement from '../components/Announcement'
import Navbar from "../components/Navbar"
import NewsLetter from "../components/NewsLetter"
import Footer from "../components/Footer"
import { login, register } from '../redux/apiCalls'
import { useDispatch, useSelector } from 'react-redux'

const Container = styled.div`

`
const RegisterContainer = styled.div`
    width: 98.9vw;
    height: 70vh;
    display: flex;
    align-content: center;
    justify-content: center;
`

const Wrapper = styled.div`
    width: 40%;
    padding: 20px;
    margin-top: 90px;
    height: 70vh;
`
const Form = styled.form`
display: flex;
flex-wrap: wrap;
    
`
const Title = styled.h1`
    font-size: 40px;
    font-weight: 500;
`
const Input = styled.input`
    flex:1;
    font-size: 16px;
    min-width: 40%;
    margin: 20px 10px 0 0;
    padding: 10px;
`
const Agreement = styled.span`
    margin: 20px 0px;
`
const Button = styled.button`
    width: 100px;
  padding: 5px ;
  border: none;
  border-bottom: 5px solid #028686;
  border-radius: 8px;
  /* background: #fab70f; */
  background: #04acac;
  color: white;
  font-size: 1.3rem;
  font-weight: bold;
  /* font-family: 'Raleway', sans-serif; */
  text-shadow: 0 -1px 0 rgba(0,0,0,0.3);
  outline: none;
  cursor: pointer;
  transition-property: background, color;
  transition-duration: 0.08s;
  &:hover {
    background: #029494;
    border-bottom-color: #017070;
  }
  &:active {
    bottom: -3px;
    border-bottom: 2px solid #005f5f;
    background: #018080;
    color: #e2d8d8;
  }
`
const Register = () => {

    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const dispatch = useDispatch();
    const { isFetching, error } = useSelector((state) => state.user);


    const handleClick = (e) => {
        e.preventDefault()
        register(dispatch, { email, password });
        login(dispatch, { email, password });
    };

    return (
        <Container>
            <Announcement />
            <Navbar />
            <RegisterContainer>
                <Wrapper>
                    <Title>CREATE AN ACCOUNT</Title>
                    <Form action="">
                        <Input placeholder="Name" onChange={(e) => setName(e.target.value)} />
                        <Input placeholder="Email" type="email" onChange={(e) => setEmail(e.target.value)} />
                        <Input placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
                        {/* <Input placeholder="Confirm Password" /> */}
                        <Agreement>
                            By creating an account, I consent ot the processing of my prsonal data in accordance with the <b>PRIVACY POLICY</b>
                        </Agreement>
                        <Button type="submit" onClick={handleClick}>CREATE</Button>
                    </Form>
                </Wrapper>
            </RegisterContainer>

            <NewsLetter />
            <Footer />
        </Container>
    )
}

export default Register