
import { SearchOutlined, ShoppingCartOutlined, FavoriteBorderOutlined } from '@mui/icons-material'
import React from 'react'
import styled from 'styled-components'
import { Link } from "react-router-dom";


const Info = styled.div`
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.2);
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
`

const Container = styled.div`
    flex:1;
    margin: 5px;
    min-width: 20vw;
    max-width: 23vw;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f5fbfb;
    position: relative;
    &:hover ${Info}{
        opacity: 1;
    }
`

const Circle = styled.div`

`

const Image = styled.img`
width: 80%;
 height: 85%;
 z-index: 1;
`



const Icon = styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    transition: all 0.3s ease;
    cursor: pointer;
    &:hover{
        background-color: #e9f5f5;
        transform: scale(1.1);
    }
`


const ProductPagingItem = ({ item }) => {

    return (
        <Container>
            <Circle />
            <Image src={item.img[0]} />
            <Info>
                <Icon>
                    <ShoppingCartOutlined />
                </Icon>
                <Icon>
                    <Link to={`/product/${item._id}`} style={{ textDecoration: 'none', color:'black' }} >
                        <SearchOutlined />
                    </Link>
                </Icon>
                <Icon>
                    <FavoriteBorderOutlined />
                </Icon>
            </Info>
        </Container>
    )
}

export default ProductPagingItem